}
img.spinning-logo {

    display: inline;
    width: 400px;
    height: 400px;
    -webkit-animation: spin infinite 2s linear;
            animation: spin infinite 2s linear;
}
  
@-webkit-keyframes spin {
      from {
          -webkit-transform:rotate(0deg);
                  transform:rotate(0deg);
      }
      to {
          -webkit-transform:rotate(360deg);
                  transform:rotate(360deg);
      }
  }
  
@keyframes spin {
      from {
          -webkit-transform:rotate(0deg);
                  transform:rotate(0deg);
      }
      to {
          -webkit-transform:rotate(360deg);
                  transform:rotate(360deg);
      }
  }
